import React from 'react';
import Logo from '../react-img/Logo.png';
import heart from '../react-img/Menu_heart.png';

function Header() {
    return (
        <div className={'header row mt-5'}>
            <a href={'/home'} className="col-6 logo">
                <img src={Logo} alt=""/>
            </a>
            <div className={'col-6 navigation'}>
                <div className="row">
                    <div className="col-2 pt-2"><a href="#">HOME</a></div>
                    <div className="col-2 pt-2"><a href="#">ABOUT US</a></div>
                    <div className="col-2 pt-2"><a href="#">OUR WORKS</a></div>
                    <div className="col-2 pt-2"><a href="#">JOURNAL</a></div>
                    <div className="col-2 pt-2"><a href="#">CONTACT</a></div>
                    <div className="col-2">
                        <img src={heart} alt=""/>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Header


